.navigation__toggle {
  display: block;
  width: 56px;
  height: 56px;
  background-color: #ffffff;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 2;
  cursor: pointer;
  border: none;
  padding: 0;
}
@media (max-width: 767px) {
  .navigation--closed .navigation__toggle::before {
    content: "";
    position: absolute;
    top: 17px;
    left: 17px;
    width: 22px;
    height: 4px;
    background-color: #81b3d2;
    box-shadow: 0 5px 0 0 #81b3d2, 0 10px 0 0 #81b3d2;
  }
  .navigation--closed .navigation__toggle:active::before {
    background-color: rgba(129, 179, 210, 0.3);
    box-shadow: 0 5px 0 0 rgba(129, 179, 210, 0.3), 0 10px 0 0 rgba(129, 179, 210, 0.3);
  }
  .navigation--opened .navigation__toggle {
    right: 0;
    background-color: #81b3d2;
  }
  .navigation--opened .navigation__toggle::before,
  .navigation--opened .navigation__toggle::after {
    content: "";
    position: absolute;
    top: 27px;
    right: 10px;
    width: 32px;
    height: 4px;
    background-color: #ffffff;
  }
  .navigation--opened .navigation__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .navigation--opened .navigation__toggle::after {
    transform: rotate(-45deg);
  }
  .navigation--opened .navigation__list {
    position: relative;
    width: auto;
    min-height: 55px;
    background: #81b3d2;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .navigation--closed .navigation__list {
    width: 100%;
    height: 56px;
    padding: 0;
    margin: 0;
  }
  .navigation--opened .navigation__logo-image {
    display: none;
  }
  .navigation--closed .navigation__logo-image {
    position: absolute;
    right: 50%;
    transform: translateX(50%);
  }
  .navigation--closed .navigation__item {
    display: none;
  }
  .navigation--opened {
    position: absolute;
    width: 100%;
  }
  .navigation__list a {
    position: relative;
    display: block;
    padding-top: 17px;
    padding-bottom: 12px;
    font-size: 18px;
    line-height: 26px;
    color: #ffffff;
    text-transform: uppercase;
    text-decoration: none;
    background-color: #81b3d2;
    text-align: center;
    box-shadow: 0 1px 0 #01010133, inset 0 1px 0 #ffffff33;
  }
  .navigation--opened .navigation__item:last-child::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 7px;
    left: 0;
    top: 214px;
    background: linear-gradient(180deg, #00000000 0%, #00000033 100%);
  }
  .navigation--nojs .navigation__toggle {
    display: none;
  }
  .navigation--nojs .navigation__wrapper {
    position: static;
    min-height: 0;
  }
  .navigation--nojs .navigation__item {
    display: block;
  }
  .navigation--nojs .navigation__item--active a {
    padding-right: 21px;
    padding-left: 21px;
    text-align: center;
  }
}
