.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background: #0000004d;
}
.modal-window--failure-closed {
  display: none;
}
.modal-window--success-closed {
  display: none;
}
.modal-window {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding-bottom: 35px;
  background-color: #ffffff;
  box-shadow: 0 7px 15px 0 rgba(0, 1, 1, 0.15);
  box-sizing: border-box;
}
